:root {
    /* colors based on tailwind's slate scale */
    /* https://tailwindcss.com/docs/customizing-colors */
    --slate-100: #f1f5f9;
    --slate-200: #e2e8f0;
    --slate-300: #cbd5e1;
    --slate-400: #94a3b8;
    --slate-500: #64748b;
    --slate-600: #475569;
    --slate-700: #334155;
    --slate-800: #1e293b;
    --slate-900: #0f172a;
    --blue-200: #bfdbfe;
    --blue-400: #60a5fa;
    --blue-600: #2563eb;
    --blue-800: #1e40af;

    /* Spacing */
    --spacing-xxs: 8px;
    --spacing-xs: 12px;
    --spacing-sm: 16px;
    --spacing-md: 24px;
    --spacing-lg: 32px;
    --spacing-xl: 64px;

    /* Font Sizes */
    --font-size-xs: 0.75rem;
    --font-size-sm: 1rem;
    --font-size-md: 1.5rem;
    --font-size-lg: 2rem;
    --font-size-xl: 2.5rem;
    --font-size-xxl: 3rem;

    /* Font Weight */
    --font-weight-1: 100;
    --font-weight-2: 300;
    --font-weight-3: 400;
    --font-weight-4: 500;
    --font-weight-5: 600;
    --font-weight-6: 800;

    /* Theme */
    --primary-background: var(--slate-600);
    --secondary-background: var(--slate-300);
}
